body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
}

.App-header {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

.react-grid-Canvas {
	background-color: rgba(0, 0, 0, 0) !important;
}

.react-grid-Grid {
	background-color: rgba(240, 240, 240, 0.7) !important;
}

.react-grid-Main {
	background-color: rgba(0, 0, 0, 0) !important;
	color: #333 !important;
}

.no-overflow {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.logger::-webkit-scrollbar {
	display: none;
}

.bg {
	/* The image used */
	background-image: url(/Playpaster/static/media/_DSC0223.c54db777.jpg);

	/* Full height */
	width: 100%;
	overflow: hidden;

	/* Center and scale the image nicely */
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
/* 
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
} */

html,
body {
	height: 100%;
}

.hidden {
	display: none;
}

/** Buttons **/

.btn {
	/* background-color: rgba(250, 250, 250, 0.6); */
	border-radius: 2em;
	/* border: 0.2em solid #1ecd97; */
	color: rgba(240, 240, 240, 0.8);
	text-shadow: 0px 3px 2px rgba(0, 0, 0, 0.9);
	cursor: pointer;
	font-size: 3vmin;
	padding: 0.7em 1.5em;
	text-transform: lowercase;
	transition: all 2s ease;
	text-decoration: none;
}

.btn:hover {
	background: #1ecd97;
	text-shadow: none;
	color: #333;
}

.btn--login {
	margin: 0 auto;
}

/** Background **/

.background {
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-size: cover;
	background-position: center center;
	-webkit-filter: blur(8em) opacity(0.6);
	        filter: blur(8em) opacity(0.6);
	position: absolute;
}

.main-wrapper {
	align-items: center;
	display: flex;
	height: 100%;
	margin: 0 auto;
	justify-content: center;
	position: relative;
	width: 80%;
	z-index: 1;
}

.container {
	align-items: center;
	display: flex;
	justify-content: center;
	height: 100%;
}

.main-container {
	flex: 1 1;
}

